import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import { addTag } from './actions'
//import { Tag } from './components'

class Tags extends React.Component{

	constructor(props) {
		super(props)

		this.handleChange = this.handleChange.bind(this)
		this.removeTag = this.removeTag.bind(this)
		this.addTag = this.addTag.bind(this)

		this.state = {
			tag : '',
		}

	}

	removeTag(e){
		e.preventDefault()
		this.props.handleRemoveTag(e.target.value)
	}

	addTagToTask(e){
		e.preventDefault()
		this.props.addTagToTask(this.state.tag)
	}

	addTag(e) {
		e.preventDefault()

		this.props.addTag(this.state.tag)
	}

	handleChange(event) {
		this.setState({
			tag: event.target.value
		})
	}

	render(){
		return(
			<div id="tags">
			<ul>
				{this.props.tags.tags.map((tag,i) => {
					return (
						<li key={i}>
							<button onClick={this.addTagToTask} value={tag} > {tag} </button>
							</li>
					)
				})}
			</ul>
				<form onSubmit={this.addTag}>
					<input type="text"  autoComplete="off" name="tags" onChange={this.handleChange} placeholder="Add New Tag" />
				</form>


			</div>
		)
	}

}

Tags.propTypes = {
	tags : PropTypes.object,
	addTag: PropTypes.func,
	handleRemoveTag: PropTypes.func,
	handleChange : PropTypes.func,
}

export default connect(
	state => ({
		tags: state.tags
	}), {
		addTag,
		addTagToTask
	}
)(Tags)



